import { LitElement, html } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import './toggle'
import {
  FilterStateManager,
  type FilterState,
  FilterChangeEvent
} from './filter-state-manager';


import install from '@twind/with-web-components'
import config from './twind.config'

const withTwind = install(config)

/**
 * V1FilterIn - A powerful filterable grid component that organizes filters into categories.
 * 
 * Supports both JSON-based categorized filtering and legacy string-based filtering for backward compatibility.
 * 
 * @example
 * ```html
 * <!-- JSON-based filtering (recommended) -->
 * <v1-filter-in>
 *   <div data-filter='{"complexity": ["high"], "language": ["javascript"]}'>
 *     High complexity JavaScript project
 *   </div>
 * </v1-filter-in>
 * 
 * <!-- String-based filtering (legacy support) -->
 * <v1-filter-in filter-label="Filter by category">
 *   <div data-filter="web frontend javascript">
 *     Frontend web project
 *   </div>
 * </v1-filter-in>
 * ```
 * 
 * @fires filter-change - Dispatched when filters are toggled
 * @fires popover-toggle - Dispatched when popover is opened/closed
 * 
 * @since 1.0.0
 * @public
 */
@customElement('v1-filter-in')
@withTwind
export class V1FilterIn extends LitElement {

/**
   * Attribute to read filter values from, defaults to 'data-filter'.
   * Supports both JSON and legacy string formats.
   */
  @property({ type: String })
  filterAttribute = 'data-filter'; // Attribute to read filter values from

/**
   * Label shown next to filter checkboxes, defaults to 'Filter by'.
   * Used in the UI to describe the filtering function.
   */
  @property({ type: String })
  filterLabel = 'Filter by';

  private filterStateManager = new FilterStateManager();

  /**
   * Current filter state containing categories and active filters.
   * Internal state managed by the filter state manager.
   */
  @property({ type: Object })
  filterState: FilterState = { categories: [], activeFilters: {} };

  /**
   * Whether the filter popover is currently open.
   * Controls visibility of the filter UI.
   */
  @property({ type: Boolean })
  isPopoverOpen: boolean = false;

  /**
   * Whether any filters are currently active.
   * Used to highlight the filter button when filters are applied.
   */
  @property({ type: Boolean })
  hasActiveFilters: boolean = false;

  /**
   * Theme variant for the component ('light', 'dark', or 'auto').
   * When 'auto', detects system preference.
   */
  @property({ type: String })
  theme: 'light' | 'dark' | 'auto' = 'auto';

  /**
   * Whether to show the arrow caret on the popover.
   */
  @property({ type: Boolean })
  showArrow: boolean = true;

  private _themeMediaQuery: MediaQueryList | null = null;
  private _themeChangeHandler: ((e: MediaQueryListEvent) => void) | null = null;

  private _handleOutsideClick = (event: Event): void => {
    const target = event.target as Element;
    
    // Check if click is outside the component entirely
    if (!this.contains(target) && this.isPopoverOpen) {
      this.isPopoverOpen = false;
    }
  };

  private _handleEscapeKey = (event: KeyboardEvent): void => {
    if (event.key === 'Escape' && this.isPopoverOpen) {
      this.isPopoverOpen = false;
    }
  };

  connectedCallback(): void {
    super.connectedCallback();
    // Initialize immediately and also listen for slot changes
    this._initializeFilters();
    this._applyFilters();
  }

  firstUpdated(changedProperties: any): void {
    super.firstUpdated(changedProperties);
    // Initialize again after first render to catch any slotted content
    this._initializeFilters();
    this._applyFilters();
    
    // Attach event listeners for outside click and escape key
    window.addEventListener('pointerdown', this._handleOutsideClick);
    window.addEventListener('keydown', this._handleEscapeKey);
    
    // Add theme change listener for reactive theme updates
    this._addThemeChangeListener();
  }

  disconnectedCallback(): void {
    super.disconnectedCallback();
    // Remove event listeners
    window.removeEventListener('pointerdown', this._handleOutsideClick);
    window.removeEventListener('keydown', this._handleEscapeKey);
    this._removeThemeChangeListener();
  }

  private _handleSlotChange(): void {
    // Re-initialize when slot content changes
    this._initializeFilters();
    this._applyFilters();
  }

  private _initializeFilters(): void {
    // Initialize filters from slotted content - only called during lifecycle hooks
    // This ensures filter state is not re-initialized when opening/closing popover
    const elements = this.querySelectorAll(`[${this.filterAttribute}]`);

    const parsedFiltersArray = Array.from(elements)
      .map(element => {
        const filterValue = element.getAttribute(this.filterAttribute);
        return filterValue ? this.filterStateManager.parseFilterAttribute(filterValue) : null;
      })
      .filter(filters => filters !== null);


    // Initialize state manager with parsed content - preserves existing state if already initialized
    this.filterState = this.filterStateManager.initializeFromContent(parsedFiltersArray);
    this._updateActiveFiltersState();
  }


  private _handleFilterChange(event: Event): void {
    const target = event.target as HTMLInputElement;
    const [categoryName, filterValue] = target.value.split(':');
    const isActive = target.checked;

    // Use state manager to handle filter toggle
    const filterChangeEvent: FilterChangeEvent = {
      categoryName,
      filterValue,
      isActive
    };

    this.filterState = this.filterStateManager.toggleFilter(filterChangeEvent);
    this._updateActiveFiltersState();
    this._applyFilters();
    this.requestUpdate(); // Force re-render to update checkboxes
  }

  private _handleIgnoreToggle(categoryName: string): void {
    // Toggle ignore state for the category
    this.filterState = this.filterStateManager.toggleIgnoreCategory(categoryName);
    this._updateActiveFiltersState();
    this._applyFilters();
    this.requestUpdate(); // Force re-render to update UI
  }

  private _updateActiveFiltersState(): void {
    this.hasActiveFilters = this._deriveHasActiveFilters();
  }

  private _deriveHasActiveFilters(): boolean {
    // Derive hasActiveFilters directly from current filter state
    // This ensures we get the most up-to-date state without redundant calls
    const stats = this.filterStateManager.getStats();
    return stats.hasAnyActiveFilters;
  }

  /**
   * Determines the effective theme based on the theme property and system preference.
   */
  private _getEffectiveTheme(): 'light' | 'dark' {
    if (this.theme === 'auto') {
      // Detect system preference
      return window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
    }
    return this.theme;
  }

  /**
   * Gets the appropriate CSS classes for the current theme.
   */
  private _getThemeClasses(): string {
    const effectiveTheme = this._getEffectiveTheme();
    return `theme-${effectiveTheme}`;
  }

  /**
   * Determines the icon state based on current filter conditions.
   */
  private _getIconState(): 'default' | 'active' | 'filtered' | 'disabled' {
    const stats = this.filterStateManager.getStats();
    
    // If no categories are available, icon is disabled
    if (this.filterState.categories.length === 0) {
      return 'disabled';
    }
    
    // If there are active filters, icon shows filtered state
    if (stats.hasAnyActiveFilters) {
      return 'filtered';
    }
    
    // If popover is open, icon shows active state
    if (this.isPopoverOpen) {
      return 'active';
    }
    
    // Default state
    return 'default';
  }

  /**
   * Gets the appropriate icon classes based on current state.
   */
  private _getIconClasses(): string {
    const iconState = this._getIconState();
    return `filter-icon-${iconState}`;
  }

  /**
   * Gets the count of active filters for badge display.
   */
  private _getActiveFilterCount(): number {
    const stats = this.filterStateManager.getStats();
    return stats.activeOptions || 0;
  }

  /**
   * Adds a theme change listener for reactive theme updates.
   */
  private _addThemeChangeListener(): void {
    if (this.theme === 'auto') {
      this._themeMediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
      this._themeChangeHandler = () => {
        this.requestUpdate(); // Trigger re-render when theme changes
      };
      this._themeMediaQuery.addEventListener('change', this._themeChangeHandler);
    }
  }

  /**
   * Removes the theme change listener.
   */
  private _removeThemeChangeListener(): void {
    if (this._themeMediaQuery && this._themeChangeHandler) {
      this._themeMediaQuery.removeEventListener('change', this._themeChangeHandler);
      this._themeMediaQuery = null;
      this._themeChangeHandler = null;
    }
  }

  private _togglePopover(): void {
    // Toggle popover visibility without re-initializing filters
    // Filter state persistence is maintained across popover open/close cycles
    this.isPopoverOpen = !this.isPopoverOpen;
  }

  private _applyFilters(): void {
    // Get all elements with the filter attribute in the light DOM
    const elements = this.querySelectorAll(`[${this.filterAttribute}]`);

    elements.forEach(element => {
      const filterValue = element.getAttribute(this.filterAttribute);
      let shouldShow: boolean;

      if (filterValue) {
        // Parse element filters and evaluate using state manager
        const elementFilters = this.filterStateManager.parseFilterAttribute(filterValue);
        const evaluationResult = this.filterStateManager.evaluateElement(elementFilters);
        shouldShow = evaluationResult.shouldShow;
      } else {
        // Use state manager to evaluate elements without filters
        shouldShow = this.filterStateManager.evaluateElementWithoutFilters();
      }

      // Use CSS display to show/hide items
      (element as HTMLElement).style.display = shouldShow ? '' : 'none';
    });
  }

  render() {
    const themeClasses = this._getThemeClasses();
    const iconClasses = this._getIconClasses();
    const activeFilterCount = this._getActiveFilterCount();
    const iconState = this._getIconState();
    
    return html`
      <!-- Apply theme classes to the entire component -->
      <div class="${themeClasses}">
        <!-- Filter icon button with popover container -->
        ${this.filterState.categories.length > 0 ? html`
          <div class="max-w-7xl mx-auto mb-4 px-4">
            <!-- Relatively positioned wrapper for icon and popover -->
            <div class="relative inline-block">
              <button 
                @click=${this._togglePopover}
                class="relative inline-flex items-center gap-2 px-3 py-2 text-sm font-medium rounded-md transition-all duration-200 ${
                  iconState === 'filtered' 
                    ? 'bg-red-50 text-red-700 ring-2 ring-red-200 hover:bg-red-100' 
                    : iconState === 'active'
                    ? 'bg-blue-50 text-blue-700 ring-2 ring-blue-200 hover:bg-blue-100'
                    : iconState === 'disabled'
                    ? 'bg-gray-50 text-gray-400 cursor-not-allowed opacity-50'
                    : 'bg-gray-50 text-gray-700 hover:bg-gray-100 hover:text-gray-900'
                } ${
                  this._getEffectiveTheme() === 'dark' ? (
                    iconState === 'filtered' 
                      ? 'dark:bg-red-900/20 dark:text-red-400 dark:ring-red-800/30 dark:hover:bg-red-900/30' 
                      : iconState === 'active'
                      ? 'dark:bg-blue-900/20 dark:text-blue-400 dark:ring-blue-800/30 dark:hover:bg-blue-900/30'
                      : iconState === 'disabled'
                      ? 'dark:bg-gray-800/20 dark:text-gray-600 dark:cursor-not-allowed dark:opacity-50'
                      : 'dark:bg-gray-800/20 dark:text-gray-300 dark:hover:bg-gray-800/30 dark:hover:text-gray-200'
                  ) : ''
                }"
                ?disabled=${iconState === 'disabled'}
                aria-label="${this.filterLabel} ${activeFilterCount > 0 ? `(${activeFilterCount} active)` : ''}"
              >
                <!-- Filter icon with dynamic styling -->
                <svg class="w-4 h-4 ${iconClasses} transition-colors duration-200" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 4a1 1 0 011-1h16a1 1 0 011 1v2.586a1 1 0 01-.293.707l-6.414 6.414a1 1 0 00-.293.707V17l-4 4v-6.586a1 1 0 00-.293-.707L3.293 7.293A1 1 0 013 6.586V4z"></path>
                </svg>
                ${this.filterLabel}
                
                <!-- Badge indicator for active filters -->
                ${activeFilterCount > 0 ? html`
                  <span class="filter-badge" title="${activeFilterCount} active filters"></span>
                ` : ''}
              </button>
              
              <!-- Popover container - absolutely positioned with enhanced styling -->
              ${this.isPopoverOpen ? html`
                <div class="absolute left-0 mt-2 w-96 max-w-sm rounded-lg z-popover popover-shadow-enhanced ${
                  this._getEffectiveTheme() === 'dark' 
                    ? 'bg-gray-800 border border-gray-700' 
                    : 'bg-white border border-gray-200'
                }" 
                  style="background-color: var(--popover-bg); border-color: var(--popover-border);">
                  
                  <!-- Optional arrow caret -->
                  ${this.showArrow ? html`
                    <div class="popover-arrow"></div>
                  ` : ''}
                  
                  <div class="max-h-96 overflow-auto">
                    <div class="flex flex-col gap-4 p-4">
                      ${this.filterState.categories.map(category => html`
                        <div class="relative py-2 border-b last:border-b-0 ${
                          this._getEffectiveTheme() === 'dark' 
                            ? 'border-gray-700' 
                            : 'border-gray-200'
                        }" style="border-color: var(--border-primary);">
                          <div class="flex justify-between items-center mb-2">
                            <span class="font-semibold whitespace-nowrap transition-colors duration-200 ${
                              this._getEffectiveTheme() === 'dark' 
                                ? 'text-gray-200' 
                                : 'text-gray-800'
                            } ${category.ignored ? 'opacity-50 pointer-events-none' : ''}" 
                              style="color: var(--text-primary);">${category.label}:</span>
                            <v1-filter-in-toggle .ignoreValue=${category.ignored} @on-click=${() => this._handleIgnoreToggle(category.name)}></v1-filter-in-toggle>
                          </div>
                          <div class="flex flex-wrap items-center gap-4 ${category.ignored ? 'opacity-50 pointer-events-none' : ''}">
                            <div class="flex gap-4 flex-wrap">
                              ${category.options.map(option => html`
                                <div class="flex items-center gap-2 cursor-pointer hover:bg-gray-50 dark:hover:bg-gray-700 rounded px-2 py-1 transition-colors duration-150" 
                                  style="--hover-bg: var(--bg-hover);">
                                  <input 
                                    type="checkbox" 
                                    id="filter-${category.name}-${option.value}" 
                                    value="${category.name}:${option.value}"
                                    .checked=${option.active}
                                    @change=${this._handleFilterChange}
                                    class="rounded border-gray-300 text-blue-600 focus:ring-blue-500 dark:border-gray-600 dark:bg-gray-700 dark:focus:ring-blue-600"
                                  >
                                  <label for="filter-${category.name}-${option.value}" 
                                    class="text-sm cursor-pointer select-none transition-colors duration-200 ${
                                      this._getEffectiveTheme() === 'dark' 
                                        ? 'text-gray-300 hover:text-gray-200' 
                                        : 'text-gray-600 hover:text-gray-800'
                                    }" 
                                    style="color: var(--text-secondary);">${option.label}</label>
                                </div>
                              `)}
                            </div>
                          </div>
                        </div>
                      `)}
                    </div>
                  </div>
                </div>
              ` : ''}
            </div>
          </div>
        ` : ''}

        <!-- Container for slotted content -->
        <div class="max-w-7xl mx-auto px-4 flex flex-wrap gap-6">
          <slot @slotchange=${this._handleSlotChange}></slot>
        </div>
      </div>
    `;
  }
}
