import { html } from 'lit';
import { component, useCallback, useState, useEffect } from 'haunted';

import install from '@twind/with-web-components'
import config from './twind.config'

const withTwind = install(config)

interface V1FilterInToggleProps {
    ignoreValue?: boolean;
}

function V1FilterInToggle(this: Element, { ignoreValue = false }: V1FilterInToggleProps) {
    const handleIgnoreToggle = useCallback(() => {
        this.dispatchEvent(new CustomEvent('on-click', {
            detail: { message: 'Hello from child' },
            bubbles: true,
            composed: false
        }));
    }, []);

    // Reactive theme detection
    const [prefersDark, setPrefersDark] = useState(
        window.matchMedia('(prefers-color-scheme: dark)').matches
    );

    useEffect(() => {
        const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
        const handler = (e: MediaQueryListEvent) => setPrefersDark(e.matches);
        
        mediaQuery.addEventListener('change', handler);
        return () => mediaQuery.removeEventListener('change', handler);
    }, []);
    
    return html`
    <button 
      class="bg-none border rounded p-1 flex items-center justify-center w-6 h-6 transition-all duration-200 ${
        ignoreValue 
          ? (prefersDark 
              ? 'bg-red-900/20 border-red-700 text-red-400 hover:bg-red-900/30' 
              : 'bg-red-100 border-red-400 text-red-600 hover:bg-red-200')
          : (prefersDark 
              ? 'border-gray-600 text-gray-400 hover:bg-gray-700 hover:border-gray-500 hover:text-gray-300' 
              : 'border-gray-400 text-gray-600 hover:bg-gray-100 hover:border-gray-500 hover:text-gray-700')
      }"
      @click=${handleIgnoreToggle}
      title="${ignoreValue ? 'Enable filtering' : 'Ignore category'}"
      style="${
        ignoreValue 
          ? 'background-color: var(--toggle-active-bg, #fef2f2); border-color: var(--toggle-active-border, #fca5a5); color: var(--toggle-active-text, #dc2626);' 
          : 'background-color: var(--toggle-bg, transparent); border-color: var(--toggle-border, #9ca3af); color: var(--toggle-text, #6b7280);'
      }"
    >
      ${ignoreValue ? html`
        <svg class="w-3.5 h-3.5" viewBox="0 0 24 24" stroke-width="1.5" fill="none" xmlns="http://www.w3.org/2000/svg" color="currentColor">
          <path d="M3 13C6.6 5 17.4 5 21 13" stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"></path>
          <path d="M12 17C10.3431 17 9 15.6569 9 14C9 12.3431 10.3431 11 12 11C13.6569 11 15 12.3431 15 14C15 15.6569 13.6569 17 12 17Z" stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"></path>
        </svg>
      ` : html`
        <svg class="w-3.5 h-3.5" stroke-width="1.5" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" color="currentColor">
          <path d="M3.99961 3H19.9997C20.552 3 20.9997 3.44764 20.9997 3.99987L20.9999 5.58569C21 5.85097 20.8946 6.10538 20.707 6.29295L14.2925 12.7071C14.105 12.8946 13.9996 13.149 13.9996 13.4142L13.9996 19.7192C13.9996 20.3698 13.3882 20.8472 12.7571 20.6894L10.7571 20.1894C10.3119 20.0781 9.99961 19.6781 9.99961 19.2192L9.99961 13.4142C9.99961 13.149 9.89425 12.8946 9.70672 12.7071L3.2925 6.29289C3.10496 6.10536 2.99961 5.851 2.99961 5.58579V4C2.99961 3.44772 3.44732 3 3.99961 3Z" stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"></path>
        </svg>
      `}
    </button>
  `;
}

// Create and register the custom element
const V1FilterInToggleElement = withTwind(component<HTMLElement & V1FilterInToggleProps>(V1FilterInToggle, { observedAttributes: ['ignoreValue'] }));
customElements.define('v1-filter-in-toggle', V1FilterInToggleElement);
